@use './mixins' as *;

.container {
  margin: 0 auto;
  padding: 0 2.5rem;
  width: 100%;
}

img:not([src]):not([srcset]) {
  visibility: hidden;
}

[data-hide-xl="true"] {
  @include respond(xl) {
    display: none !important;
  }
}

[data-hide-up-xl="true"] {
  @include respond(up-xl) {
    display: none !important;
  }
}

[data-hide-mid="true"] {
  @include respond(mid) {
    display: none !important;
  }
}

[data-hide-xs="true"] {
  @include respond(xs) {
    display: none !important;
  }
}

a:link {
  text-decoration: none;
}