@use '../../common/styles/mixins' as *;

.searchbar {
  --gutter-horizontal: 5rem;
  --gutter-vertical: 3rem;
  --offset-height: 4rem;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  z-index: 999999;
  background: #fff;
  transform: translateY(-100%);
  opacity: 0;
  visibility: hidden;
  transition: all .25s ease-in;
  padding: 0 var(--padding-mobile, 2rem);
  height: 100vh;
  overflow-y: auto;
  @media screen and (min-width: 990px) {
    padding: 0 var(--padding-desktop, 4rem);
    height: auto;
  }

  &--hasContent {
    height: 85vh;
    @media screen and (min-width: 1485px) {
      height: min(85vh, 63rem);
    }
    @media screen and (min-width: 1790px) {
      height: min(85vh, 70rem);
    }
    @media screen and (min-width: 2150px) {
      height: min(85vh, 75rem);
    }
    transform: none!important;
  }
  
  @include respond(mid) {
    --gutter-horizontal: 2rem;
    --gutter-vertical: 1rem;
    --offset-height: 0;
  }

  &__form {
    width: 100%;
    height: 7rem;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 4rem;
    position: relative;
    margin: 0 auto;

    @media screen and (min-width: 990px) {
      width: 85%;
    }

    svg {
      width: 2rem;
      height: 2rem;
    }
  }

  &__wrapper {
    flex-grow: 1;
    display: flex;
    gap: 4rem;
    align-items: center;
  }

  &__error {
    position: absolute;
    bottom: 0;
    left: 0;
    font-size: 12px;
    background: var(--error);
    color: #fff;
    margin-bottom: 3px;
    padding: 0 3px;
    display: none;
    &.active {
      display: block;
    }
  }

  &__backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 999998;
    display: none;
  }

  &__input {
    width: 100%;
    height: auto;
    border: none;
    outline: none;
    border-radius: 0!important;
    font-size: 16px;
    text-transform: uppercase;
    font-family: var(--body-text);
    font-weight: 500;
    border-bottom: 1px solid #000; 

    &:focus,
    &:focus-visible,
    &:focus-within { 
      outline: none;
      box-shadow: none;
    }

    &::placeholder {
      text-transform: uppercase;
      color: #6e6e6e;
      font-family: inherit;
      font-weight: inherit;
    }
  }

  .search_btn {
    display: flex!important;
    align-items: center;
    @media screen and (max-width: 770px) {
      display: none!important;
    }
  }

  &__results-head {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid rgba(0,0,0,.1);
    padding-bottom: 1rem;
    margin-bottom: 3rem;
    font-family: 'Roboto';
    text-transform: uppercase;
    font-size: 1.1rem;
    font-weight: 500;
    color: #6e6e6e;
  }

  &__loader {
    min-height: 15rem;
    height: 30vh;
    max-height: 30rem;
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
  }

  &__results-body {
    display: flex;
    justify-content: space-between;
    flex-direction: column-reverse;
    gap: 2rem;

    @media screen and (min-width: 990px) {
      flex-direction: row;
    }

    .predictive--products {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(230px, 1fr));
      gap: 1rem .5rem;
      margin-bottom: 1.5rem;
      flex-grow: 1;


      @media screen and (min-width: 990px) {
        max-height: 450px;
        overflow-y: auto;
      }
      
      @include respond(tablet) {
        grid-template-columns: repeat(2, minmax(min-content, 1fr)) !important;
      }
      
      @include respond(sm) {
        grid-template-columns: minmax(min-content, 1fr) !important;
      }
    }
    .predictive--queries {
      &:empty {
        display: none;
      }
    }
    .predictive-search--queries {
      @media screen and (min-width: 990px) {
        max-height: 450px;
        overflow-y: auto;
      }
      ul {
        list-style: none;
        padding: 0;
        margin: 0;
      }

      li {
        &:active,
        &:hover {
          color: var(--dark-silver)
        }
        a {
          color: inherit
        }
      }
    }
    .searchbar.searchbar--active {
      border-top: 1px solid #000;
    }
    
    .suggested-qury__item-heading {
      mark {
        background: transparent;
        font-weight: 700;
        color: inherit;
      }
    }
  }

  &__searchbar__btn-view-all {
    color: #6e6e6e;
  } 

  &__icon-search,
  &__btn-close {
    position: absolute;
    top: 0;
    z-index: 1;
    height: 100%;
    display: flex;
    align-items: center;
  }

  &__icon-search {
    left: 0;
    line-height: normal;
  }

  &__btn-close {
    right: 0;
  }

  &--active {
    opacity: 1;
    visibility: visible;
    transform: translate(0);

    @include respond(mid) {
      height: 100vh;
      padding-bottom: calc(var(--header-height) + var(--announcement-banner-height));
    }
  }

  &--active + &__backdrop {
    display: block;
  }
}

body .searchbar.searchbar--active {
  border-top: 1px solid #000;
}

.searchbar__btn-view-all {
  display: inline-block;
}

.searchar__results-footer {
  display: flex;
  justify-content: center;
  margin: 2rem 0;
}

.searchar__results-footer .no-results,
.searchar__results-footer.empty .searchbar__btn-view-all {
  display: none;
}

.searchar__results-footer .searchbar__btn-view-all,
.searchar__results-footer.empty .no-results {
  display: initial;
}

@media screen and (max-width: 750px) {
  .searchbar__results .product-item__footer {
    min-height: 6rem;
  }
}

@media screen and (max-width: 400px) {
  .searchbar__btn-view-all.Button {
    max-width: 100%;
    padding: 1rem 1.5rem;
    display: flex;
    justify-content: center;
    text-align: center;
  }
}

@media screen and (min-width: 1025px) {
  .searchbar .product-item__footer {
    min-height: 9rem;
  }
}

