@use './mixins' as *;

.product-item {
  font-family: var(--heading-font-family);
  display: flex;
  flex-direction: column;
  margin-bottom: 1px;
  position: relative;

  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  &--group {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 2.65%;
    grid-column: 1 / -1;

    &[data-layout="with_image"] {
      gap: 0 2px;
    }

    &-row {
      grid-row-start: var(--desktopRow);
      @media screen and (max-width: 1008px) {
        grid-row-start: var(--mobileRow)
      }
    }
  }

  &__backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 9995;
    visibility: hidden;
  }

  &__backdrop-upper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(var(--header-height) + var(--announcement-banner-height) + var(--plp-subnav-height) + var(--filters-bar-height));
    z-index: 9995;
    visibility: hidden;

    &.active {
      visibility: visible;
    }
  }

  &__toolbar {
    position: absolute;
    bottom: 1rem;
    left: 0;
    width: 100%;
    display: flex;
    z-index: 998;
  }

  &__backdrop.active ~ &__toolbar {
    z-index: 999;
  }

  &__label {
    position: absolute;
    top: 0;
    right: 0;
    padding: 10px;
    font-size: 1.2rem;
    display: flex;
    align-items: center;
    z-index: 3;
    color: #444;
  }

  &__icon {
    width: 12px;
    height: 12px;
    margin-right: 7px;
    
    &--pause {
      width: 14px;
      height: 14px;
      display: none;
    }
  }

  &.playing &__icon--pause {
    display: inline-block !important;
  }
  
  &.playing &__icon--play {
    display: none;
  }

  &__option {
    color: #323232;
    font-weight: 500;
    font-family: Roboto;
    letter-spacing: 0px;
    text-transform: uppercase;
    font-size: 1rem;
    padding: 8px 1rem;
    transition: background-color .2s ease-in;
    display: flex;
    align-items: center;
    cursor: pointer;

    &__icon {
      display: inline-block;
      height: 1em;
      width: 1em;
      fill: currentColor;
      vertical-align: middle;
      stroke-width: 1px;
      background: 0 0;
      pointer-events: none;
      margin-left: 5px;
    }

    &:disabled {
      opacity: .5;
    }
  }
  
  &--group &--group {
    gap: 0 2px;
    grid-column: 2 / -1;
  }

  &[data-featured-position="right"] &--featured {
    @media screen and (min-width: 1009px) {
      grid-column: 2 / -1;
      margin-bottom: 14.2rem;
    }
  }
  &[data-featured-position-mobile="bottom"] &--featured {
    @media screen and (max-width: 1008px) {
      grid-row: 2/-1
    }
  }

  &[data-featured-position="right"] &--group {
    @media screen and (min-width: 1009px) {
      grid-column: 1 / 2;
      grid-row: 1 / 2 !important; 
    }
  }

  &[data-featured-position-mobile="bottom"] &--group {
    @media screen and (max-width: 1008px) {
      grid-row: 1!important;
    }
  }

  &--featured {
    overflow: hidden;
    position: relative;
    margin-bottom: 14.2rem;
  }

  &__body {
    width: 100%;
    position: relative;
    overflow: hidden;
    flex-grow: 1;
    
    .labels-new-wrapper {
      display: none;
      position: absolute;
      top: 2rem;
      right: 2rem;
      z-index: 1;

      @media screen and (min-width: 1140px) {
        display: block;
      }
    }
  }

  &__body &__link {
    background: var(--card-background, #e3e3e3);
    height: 0;
    padding-bottom: 135%;
  }

  &__tag {
    position: absolute;
    margin: 4px 0;
    padding: 2px 6px;
    font-size: 1.2rem;
    background: #fff;
    z-index: 10;
    top: 7.5px;
    left: 1rem;
    font-family: inherit;
    font-weight: 500;
  }

  &__media {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    object-fit: cover;
    object-position: center;
    transition: all .2s ease;

    &:not(:nth-child(2n)) {
      animation: fadeIn .75s ease;
      animation-fill-mode: backwards;
    }

    &--image:first-child:not(:only-child) + &--image:nth-child(2) {
      opacity: 0;
      z-index: 2;

      @media screen and (min-width: 1125px) {
        &:hover { opacity: 1; }
      }
    }

    &--video {
      width: 108%;
      left: -5%;
      opacity: 0;
      transition: opacity .5s ease;
      z-index: -1;
    }
  }

  &.playing &__body:not(.active) &__media--video {
    opacity: 1 !important;
    z-index: 1;
  }

  &__footer {
    margin-top: .5rem;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    width: 100%;
    margin-bottom: 0;
    padding: 0 1rem;
    min-height: 12rem;

    @media screen and (min-width: 1025px) {
      min-height: 12rem;
    }

    span.sold-out{
      display: none;
      color: var(--error);
      text-align: start;
    }

    .labels-new-wrapper {
      margin-block: .5rem 1.5rem;
      @media screen and (min-width: 1140px) {
        display: none;
      }
    } 
  }

  &__footer &__link {
    display: inline-block;
    width: 100%;
  }

  &__footer[data-available="false"] {
    span.sold-out{
      display: block;
    }
  }

  &__price-group {
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
  }

  &__link {
    width: 100%;
    display: block!important;
    position: relative;
    text-align: center;
    overflow: hidden;
  }

  &__dropdown {
    position: absolute;
    bottom: 0;
    left: 0;
    width: calc(100% + 1px);
    opacity: 0;
    visibility: hidden;
    z-index: 11;
    display: flex;
    transition: all .15s ease;
    box-shadow: 0 -2px 10px rgba(54, 54, 54, .2);
    overflow: auto;

    &::-webkit-scrollbar {
      height: 5px;
      width: 15px;
    }

    &::-webkit-scrollbar-thumb {
      background: #777;
    }

    &::-webkit-scrollbar-button {
      display: none;
    }

    &__item {
      text-align: left;
      text-transform: uppercase;
      padding: 12px;
      color: #323232;
      font-weight: 500;
      font-size: .9rem;
      font-family: Roboto;
      background: #f9f9f9;
      flex: 1 1 0px;
      cursor: pointer;
      
      &.disabled {
        text-decoration: line-through;
        color: #9b9b9b;
        cursor: not-allowed;
      }

      &:hover {
        background: darken(#fff, 6%);
      }
    }
  }

  &__option.active + &__dropdown {
    opacity: 1;
    visibility: visible;
  }

  &__link:hover &__image:last-child {
    opacity: 1;
  }

  &__toolbar-wrapper {
    position: absolute;
    top: 2rem;
    right: 2rem;
    display: flex;
    flex-direction: column;
    height: max-content;
    width: calc(100% + 1px - 4rem);
  }

  &[data-available=false] &__toolbar-wrapper {
    display: none;
  }

  &--layout_2 &__toolbar {
    position: static;
    flex-direction: column;
    height: max-content;
    width: 100%;
    visibility: hidden;
    opacity: 0;
    transition: opacity .3s ease;
  }

  &--layout_2 &__dropdown {
    box-shadow: none;
    height: max-content;
    top: 2rem;
    position: static;
    opacity: 1;
    visibility: visible;

    &__item {
      height: max-content;
      background: transparent;
      padding: 0;
      flex: initial;
      font-size: 1.4rem;
      font-weight: 300;

      &::before { height: .5px; }

      &:not(:last-child) { 
        margin-right: 2rem;

        @media screen and (max-width: 650px) {
          margin-right: 1rem
        }
      }
    }

    &::-webkit-scrollbar {
      height: 2px;
    }
  }

  &--layout_2 &__option-wrapper {
    height: max-content;
    
    &:not(:first-child) {
      margin-top: .5rem;
      padding-top: 1rem;
      border-top: 1px solid rgba(54, 54, 54, .4);
    }
  }

  &--layout_2 &__option {
    padding: 0;
    margin-bottom: 1rem;
    font-size: 1.4rem;
  }

  &__cart-btn {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 10;
    color: transparent;
    display: flex;
    flex-direction: column;
    text-transform: uppercase;
    transition: color .3s ease;
    align-items: flex-end;
    font-size: 1.2rem;
    max-width: 90%;
    padding: 1rem;
    margin: -1rem;


    svg {
      width: 1.4rem;
      height: 1.4rem;
      margin-bottom: 1rem;
      fill: #000;

      @media screen and (min-width: 1140px) {
        opacity: 0;
      }
    }

    &[data-acsb-focused=true] {
      svg {
        @media screen and (min-width: 1140px) {
          opacity: 1;
        }
      }
    }

    .expanded {
      width: max-content;
      max-width: 100%;
    }

    .expanded + .short {
      display: none;
    }

    .short {
      transform: translateX(.5rem);
    }

    &.active { color: #323232; }
  }

  &--layout_2 &__label {
    top: 1.5rem;
    left: 3rem;
    right: initial;
    padding: 0;
    font-size: 1.4rem;

    @include respond(mid) {
      top: 1rem;
      left: 1rem;
      font-size: 1.2rem;
    }
  }

  &__btn-toolbar-toggle {
    position: absolute;
    right: 0;
    bottom: .85rem;
    z-index: 11;
    display: flex;
    margin-right: -.95rem;
    opacity: 0;
    visibility: hidden;

    svg {
      width: auto;
      height: .85rem;
      transition: transform .3s ease;
    }

    @include respond(mid) { display: none; }
  }

  &__toolbar-wrapper &__toolbar { z-index: 9; }

  &__body.active &__label {
    display: none;
  }

  &__body.active &__toolbar,
  &__body.active &__btn-toolbar-toggle {
    opacity: 1;
    visibility: visible;
  }

  &__body.active &__media {
    height: 90%;
    top: calc(var(--options-size) * 6%);
    @media screen and (max-width: 1200px) {
      top: calc(var(--options-size) * 8%);
    }
    @media screen and (max-width: 1008px) {
      top: calc(var(--options-size) * 6%);
    }
    @media screen and (max-width: 600px) {
      top: calc(var(--options-size) * 8%);
    }
    @media screen and (max-width: 500px) {
      top: calc(var(--options-size) * 11%);
    }
    @media screen and (max-width: 400px) {
      top: calc(var(--options-size) * 15%);
    }
  }

  &__body.active &__toolbar-wrapper { z-index: 9996; } 

  &__body.active &__btn-toolbar-toggle svg {
    transform: rotate(180deg);
  }

  &--layout_2 .wishlist-engine {
    @media screen and (min-width: 1140px) {
      opacity: 0;
    }
    position: absolute;
    top: 1.5rem;
    left: 1.5rem;
    z-index: 9;
    margin-top: -3px;
    margin-left: -5px;

    @media screen and (max-width: 485px) {
      top: .5rem;
      left: .5rem;
    }

    .wishlist-engine-button {
      background: transparent!important;
    }
  }

  &--layout_2:hover &__body:not(.active) &__cart-btn > *,
  &--layout_2:hover ~ &__cart-btn > *,
  &--layout_2 ~ &__cart-btn:hover > *,
  &--layout_2:hover &__body:not(.active) ~ .wishlist-engine {
    opacity: 1!important;
  }

  &--layout_2:hover &__body .labels-new-wrapper,
  &__body.active .labels-new-wrapper {
    opacity: 0;
  }

  &__body.active &__cart-btn > svg {
    opacity: 1!important;
  }

  &__body.active &__backdrop {
    visibility: visible;
  }

  &__body.active ~ .wishlist-engine {
    display: none;
  }

  &__animate-btn {
    animation: scaleUp 1s ease-in;
  }

  &--layout_2 .product-title,
  &--layout_2 .product-color-link {
    text-align: left;
    font-weight: inherit;
    font-size: inherit;
    color: #000;
  }

  &--layout_2 .product-color-link { color: #757575; }

  &--layout_2 &__footer {
    margin-top: 2rem;
    font-size: 1.4rem;
    font-weight: 400;
  }

  &--layout_2 &__price-group {
    align-items: flex-start;
  }

  @include respond(sm) {
    &--layout_2 &__toolbar-wrapper {
      width: calc(100% - 2rem);
      right: 1rem;
      top: 1rem;
    }
  }

  @media screen and (max-width: 1008px) {
    &__label { cursor: pointer; }

    &--featured {
      grid-column: 1 / -1 !important;
      grid-row: 1 / 2; // initial
      margin-bottom: 1px;
    }

    &--group {
      grid-template-columns: 1fr;
      grid-template-rows: auto auto;
      gap: 3.5px;
    }

    &--group &--group {
      grid-column: 1 / -1;
      grid-template-columns: 1fr 1fr;
      grid-row: 2 / 3 !important; // 1/3
    }
  }

  @media screen and (max-width: 769px) {
    &[data-layout="with_image"] &--featured {
      margin-bottom: 3rem;
      margin-top: 0;
    }
  }

  &__wrapper {
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
  }

  .wishlist-engine {
    position: absolute;
    top: 1.5rem;
    left: 1.5rem;
    z-index: 9;
    @media screen and (max-width: 485px) {
      top: .5rem;
      left: .5rem;
      .wishlist-engine-button {
        svg {
          width: 1.6rem;
          height: 1.6rem;
        }
      }
    }
  
    .wishlist-engine-button {
      background-color: transparent!important;
    }
  }
}

@media screen and (max-width: 1024px) { 
  .collections--bottom .product-item--featured {
    grid-row: initial !important;
  }
  .collections--bottom .product-item--group {
    grid-row: 1/3 !important;
  } 
}

.btn-add-to-cart {
  display: inline-block;
  width: max-content;
  padding: 12px 8px;
  font-size: 1rem;
  text-transform: uppercase;
  text-align: center;
  font-family: "Roboto";
  visibility: hidden;
  position: absolute;
  right: 5px;
  z-index: 9998;
  letter-spacing: 0;
  bottom: 0;

  &::before {
    content: "";
    position: absolute;
    bottom: 10px;
    left: 7px;
    right: 7px;
    height: 1px;
    border-bottom: 1px solid #000;
    transform-origin: 100% 50%;
    transform: scale3d(0, 1, 1);
    transition: transform 0.3s;
  }

  &:hover::before {
    transform-origin: 0% 50%;
    transform: scale3d(1, 1, 1);
  }
}

.collections .product-item__toolbar {
  bottom: 0;
}

.product-color-link,
.product-price,
.labels-new-wrapper .label--new {
  font-size: 1.25rem;
  font-weight: 400;
  font-family: inherit;
  font-style: var(--heading-font-style);
  transition: color 0.2s ease-in-out;
  color: var(--black);
}

.product-color-link {
  color: var(--dark-silver)
}

.labels-new-wrapper .label--new {
  display: block;
  color: var(--new-label-color_secondary);
  background-color: var(--new-label-background_secondary);
  text-transform: uppercase!important;
  line-height: normal!important;
  padding: .6rem 1.2rem;
  font-size: 1rem;

  @media screen and (min-width: 1140px) {
    color: var(--new-label-color_primary);
    background-color: var(--new-label-background_primary);
    font-size: 1.2rem;
  }
}

.product-price {
  text-transform: uppercase;
}

.product-price--real {
  margin-right: 10px;
  position: relative;
  color: var(--dark-silver)
}

.product-price--real::before {
  position: absolute;
  content: "";
  top: 50%;
  left: -0.4em;
  width: calc(100% + .8em);
  height: 1px;
  background: #757575;
  z-index: 10;
}

.product-title {
  text-align: left;
  display: inline-block;
  font-weight: 500;
  font-family: inherit;
  line-height: 1.5em;
  font-size: 1.25rem;
  padding: 0;
  color: #424242;
  width: 100%;
}

.product-price,
.product-title {
  font-family: var(--body-font)
}

@keyframes scaleUp {
  0% { transform: scale(1.35); }
  100% { transform: scale(1); }
}

.product-item__body.active .short {
  color: var(--black)
}

.shopify-section-header-sticky ~ main .product-item__backdrop-upper {
  height: calc(var(--header-height)  + var(--plp-subnav-height) + var(--filters-bar-height) - 4px);
}

.shopify-section-header-hidden ~ main .product-item__backdrop-upper {
  height: calc(var(--plp-subnav-height) + var(--filters-bar-height));
}

.banner-content {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 9;
  display: flex;
  flex-direction: column;
  width: 80%;
  align-items: center;
  justify-content: center;
  transform: translate(-50%, -50%);

  a {
    margin-top: 1rem;
  }

  & > * {
    margin: 0;
    color: inherit!important;
    text-align: center;
  }
}

.banner__footer {
  min-height: 12.2rem;
  margin-top: 2rem;
  padding: 0 1rem;

  @media screen and (max-width: 1008px) {
    min-height: initial;
    margin: 2rem 0
  }
}
.imageBannerWrapper {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
}

.wrapper-main {
  @media screen and (max-width: 1008px) {
    padding-bottom: 150%;
  }
}

.product-item--featured {
  margin-bottom: 0!important;
}

.banner-center .banner__footer,
.banner-bottom .banner__footer {
  .text {
    display: none;
  }
}
.banner-below .banner-content  {
  display: none;
}

.banner__footer {
  padding-bottom: var(--padding-bottom);
  a:hover {
    color: var(--dark-silver)!important 
  }
  a:active {
    color: var(--silver)
  }
  .text {
    display: block;
  }
}

.banner-bottom {
  .banner-content {
    top: calc(100% - 4rem);
    transform: translate(-50%, -100%);
  }
}

.product-item__option-wrapper:nth-child(1) {
  .product-item__dropdown {
    max-width: calc(100% - 30px);
  }
}