button {
  border: none;
  background: transparent;
  font-size: inherit;
  cursor: pointer;

  &:focus { outline: none; }
}

.Button {
  font-size: 1.2rem;
  padding: 1rem 8rem;
  text-transform: uppercase;
  cursor: pointer;
  font-family: 'GalanoGrotesqueRegular', sans-serif;
  font-weight: 400;
  font-style: normal;
  line-height: 2;
}

.Button[disabled] {
  cursor: not-allowed;
}

.Button-primary {
  color: var(--white)!important;
  background: var(--black);
  border: 1px solid var(--black);
}

.Button-primary:hover {
  background: var(--dark-silver);
  border-color: var(--dark-silver);
}

.Button.Button-primary-outline.Button-disabled,
.Button.Button-primary.Button-disabled {
  cursor: not-allowed;
  background: var(--silver);
  color: var(--white)!important;
  border-color: var(--silver);
}

.Button.Button-secondary.Button-disabled {
  cursor: not-allowed;
  background: var(--white);
  color: var(--silver)!important;
  border: 1px solid var(--silver)
}

.Button.Button-tertiary.Button-disabled {
  cursor: not-allowed;
  color: var(--silver)!important;
}

.Button-primary-outline {
  color: var(--black)!important;
  background: transparent;
  border: 1px solid var(--black)
}

.Button-primary-outline:hover {
  color: var(--white)!important;
  background: var(--black)
}

.Button-primary-outline:focus {
  border: 1px solid transparent;
  outline: 2px solid var(--blue)
}

.Button-primary-outline.Button-inverted {
  color: var(--white)!important;
  background: transparent;
  border: 1px solid var(--white)
}
.Button-primary-outline.Button-inverted:hover {
  background: var(--black);
  border-color: var(--black)!important;
}
.Button-primary-outline.Button-inverted:focus {
  border: none;
  outline: 2px solid var(--blue)
}

.Button-secondary {
  padding: 1rem 6rem;
  color: var(--black)!important;
  background: transparent;
  border: 1px solid var(--black);
}

.Button-secondary.Button-inverted {
  color: var(--white)!important;
  border: 1px solid var(--white);
}

.Button-secondary.Button-inverted:hover {
  border: 1px solid var(--black);
}

.Button-secondary:hover { 
  color: var(--white)!important;
  background: var(--black);
}

.Button-secondary:focus {
  border: none;
  outline: 2px solid var(--blue)
}

.Button--full {
  width: 100%;
}

.Button--stretched {
  padding-left: 40px;
  padding-right: 40px;
}

.Button-tertiary {
  border: none;
  color: var(--black);
  font-size: 1.4rem;
  text-decoration: underline!important;
  text-underline-offset: 4px;
  padding: 0;
}

.Button-tertiary:hover{
  color: var(--dark-silver)!important;
}
.Button-tertiary.Button-inverted:hover {
  color: var(--silver)!important;
}

.Button-tertiary:focus {
  border: none;
  outline: 2px solid var(--blue)
}

.Button-tertiary.Button-inverted {
  color: #fff!important;
}

@media screen and (max-width: 1139px) {
  .Button {
    padding: 1rem 3rem;
  }
  .Button-secondary {
    padding: 0.4rem 4rem;
  }
  .Button-tertiary {
    padding: 0;
  }
}

.Button--small {
  font-size: calc(var(--base-text-font-size) - (var(--default-text-font-size) - 10px));
  padding: 12px 24px;
}

.Button__SeparatorDot {
  display: inline-block;
  margin: 0 18px;
  content: "";
  height: 3px;
  width: 3px;
  border-radius: 100%;
  background: currentColor;
}

/* We allow those buttons to have secondary state. The secondary state is displayed (for instance during loading time) when
 the class "Button--secondaryState" is added */
.Button__PrimaryState,
.Button__SecondaryState {
  display: block;
  transition: transform 0.4s cubic-bezier(0.75, 0, 0.125, 1), opacity 0.4s cubic-bezier(0.75, 0, 0.125, 1);
}

.Button__SecondaryState {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 100%;
  text-align: center;
  opacity: 0;
  transform: translate(-50%, 100%);
}

.Button--secondaryState .Button__PrimaryState {
  opacity: 0;
  transform: translateY(-100%);
}
.Button--secondaryState .Button__SecondaryState {
  opacity: 1;
  transform: translate(-50%, -50%);
}

.Button.Button__background {
  background: #00000033;
}

.Button--dark {
  color: #fff !important;
  border-color: #000;
  background: #000;
}

.Button--dark:hover {
  border-color: rgba(0, 0, 0, 0.75) !important;
  background: rgba(0, 0, 0, 0.75) !important;
}

.Button--primary::before {
  background-color: var(--button-background);
}

.Button--primary.Button--primary-inverted {
  color: #000 !important;
  border-color: #fff !important;
  background-color: #fff !important;
  transition: all 0.15s;
}

.Button--primary.Button--primary-inverted:hover {
  background-color: rgba(255, 255, 255, 0.85) !important;
  border-color: rgba(255, 255, 255, 0.85) !important;
}

.Button--outlined { border: 1px solid; }